import React from 'react';
import ReactDOM from 'react-dom';
import { HashRouter as Router } from 'react-router-dom';
import App from './App';
import registerServiceWorker from './registerServiceWorker';
import '../node_modules/bootstrap/dist/css/bootstrap.min.css';
import '../node_modules/font-awesome/css/font-awesome.min.css';
import './index.css';


import ReactGA from 'react-ga';

ReactGA.initialize('UA-105073242-1');

function logPageView() {
  console.log('page view logged as', window.location.href);
  ReactGA.set({ page: window.location.href });
  ReactGA.pageview(window.location.href);
}

ReactDOM.render(<Router onEnter={logPageView}><App /></Router>, document.getElementById('root'));
registerServiceWorker();



